import React from 'react';
import {StyleSheet, TextInput} from 'react-native';
import './TextInput.css';

const TextInputExample = ({pronoun, answer, showAnswer}) => {
  const [text, onChangeText] = React.useState('');
  const isCorrect = text == answer ? true : false;
  
  if (showAnswer && isCorrect) {
    return (
      <div className="TextInput">
        <p>
        </p>
        <p className="CorrectMark">
          
        </p>
        <TextInput
          onChangeText={onChangeText}
          style={styles.input}
          value={text}
        />
        <p>
          {pronounsArabicDict[pronoun]}
        </p>
      </div>
    );
  } 
  else if (showAnswer==true && isCorrect==false) {
    return (
      <div className="TextInput">
        <p className="IncorrectAnswer">
          {answer}
        </p>
        <p className="IncorrectMark">
          
        </p>
        <TextInput
          onChangeText={onChangeText}
          style={styles.input}
          value={text}
        />
        <p>
          {pronounsArabicDict[pronoun]}
        </p>
      </div>
    );
  }
  else {
    return (
      <div className="TextInput">
        <p>
        </p>
        <p>
        </p>
        <TextInput
          onChangeText={onChangeText}
          style={styles.input}
          value={text}
        />
        <p>
          {pronounsArabicDict[pronoun]}
        </p>
      </div>
    );
  }
};

const styles = StyleSheet.create({
  input: {
    height: 50,
    margin: 12,
    borderWidth: 1,
    padding: 10,
    textAlign: 'center',
    fontSize: 20,
  },
});

const pronounsArabicDict = {
  "3sm": "",
  "3sf": "",
  "2sm": "",
  "2sf": "",
  "1s": "",
  "3dm": "(m) ",
  "3df": "(f) ",
  "2d": "",
  "3pm": "",
  "3pf": "",
  "2pm": "",
  "2pf": "",
  "1p": "",
}

export default TextInputExample;